/* Construction-themed Professional UI Styles with Left Sidebar */
.app-container {
  min-height: 100vh;
  background: linear-gradient(
    135deg,
    #1a1a1a 0%,
    #2d2d2d 50%,
    #1a1a1a 100%
  );
  background-image: 
    url('../public/Project_Management_Tracker.jpg'),
    linear-gradient(135deg, rgba(26, 26, 26, 0.9) 0%, rgba(45, 45, 45, 0.9) 50%, rgba(26, 26, 26, 0.9) 100%);
  background-size: cover, auto;
  background-position: center, center;
  background-attachment: fixed, scroll;
  color: #ffffff;
  font-family: 'Inter', 'Roboto', -apple-system, BlinkMacSystemFont, sans-serif;
  line-height: 1.6;
  display: flex;
  flex-direction: column;
}

/* Banner Header with Top-Right Controls */
.app-header {
  background: linear-gradient(
    135deg,
    rgba(244, 63, 94, 0.95) 0%,
    rgba(239, 68, 68, 0.95) 25%,
    rgba(249, 115, 22, 0.95) 50%,
    rgba(245, 158, 11, 0.95) 75%,
    rgba(234, 179, 8, 0.95) 100%
  );
  background-image: 
    url('../public/PMT_Banner.jpg'),
    linear-gradient(135deg, rgba(244, 63, 94, 0.8) 0%, rgba(239, 68, 68, 0.8) 25%, rgba(249, 115, 22, 0.8) 50%, rgba(245, 158, 11, 0.8) 75%, rgba(234, 179, 8, 0.8) 100%);
  background-size: cover, auto;
  background-position: center, center;
  backdrop-filter: blur(10px);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
  border-bottom: 3px solid #fbbf24;
  position: relative;
  overflow: visible;
  z-index: 100;
}

.app-header::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(
    45deg,
    rgba(255, 255, 255, 0.1) 0%,
    transparent 50%,
    rgba(255, 255, 255, 0.1) 100%
  );
  pointer-events: none;
}

.header-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 1.5rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  z-index: 1;
}

/* Top-Right Controls Container */
.header-right {
  display: flex;
  align-items: center;
  gap: 1.5rem;
}

/* Live Date & Time Styles */
.live-datetime {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  background: rgba(255, 255, 255, 0.1);
  padding: 0.75rem 1rem;
  border-radius: 12px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.date-display {
  font-size: 0.9rem;
  font-weight: 600;
  color: #fbbf24;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
}

.time-display {
  font-size: 1.1rem;
  font-weight: 700;
  color: #ffffff;
  font-family: 'Courier New', monospace;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
}

/* Language Selector Styles */
.language-selector {
  position: relative;
}

.language-button {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 12px;
  padding: 0.75rem 1rem;
  color: white;
  cursor: pointer;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
  font-weight: 600;
}

.language-button:hover {
  background: rgba(255, 255, 255, 0.2);
  border-color: rgba(251, 191, 36, 0.5);
  transform: translateY(-2px);
}

.language-dropdown {
  position: absolute;
  top: calc(100% + 0.5rem);
  right: 0;
  background: rgba(15, 23, 42, 0.98);
  border: 2px solid rgba(251, 191, 36, 0.3);
  border-radius: 12px;
  padding: 0.5rem;
  min-width: 150px;
  backdrop-filter: blur(25px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.5);
  z-index: 999999;
  animation: slideDown 0.3s ease;
}

.language-option {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 0.75rem 1rem;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s ease;
  color: white;
  background: transparent;
  border: none;
  width: 100%;
  text-align: left;
  font-weight: 500;
}

.language-option:hover {
  background: rgba(251, 191, 36, 0.2);
  color: #fbbf24;
}

.language-option.active {
  background: rgba(251, 191, 36, 0.3);
  color: #fbbf24;
  font-weight: 700;
}

.flag {
  font-size: 1.2rem;
}

.dropdown-arrow {
  font-size: 0.8rem;
  transition: transform 0.3s ease;
}

/* Main Layout Container */
.main-layout {
  display: flex;
  flex: 1;
  min-height: calc(100vh - 140px); /* Adjust for header height */
}

/* Navigation Toggle & Tab */
.nav-toggle-container {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.nav-toggle {
  background: rgba(251, 191, 36, 0.2);
  border: 2px solid rgba(251, 191, 36, 0.3);
  border-radius: 8px;
  padding: 0.5rem;
  cursor: pointer;
  color: #fbbf24;
  font-size: 1.2rem;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
}

.nav-toggle:hover {
  background: rgba(251, 191, 36, 0.3);
  border-color: rgba(251, 191, 36, 0.5);
  transform: scale(1.05);
}

.nav-toggle-active {
  background: rgba(251, 191, 36, 0.4);
  border-color: rgba(251, 191, 36, 0.6);
  box-shadow: 0 0 15px rgba(251, 191, 36, 0.3);
}

.nav-tab {
  background: rgba(15, 23, 42, 0.95);
  border: 2px solid rgba(251, 191, 36, 0.3);
  border-radius: 0 8px 8px 0;
  padding: 0.5rem 1rem;
  color: #fbbf24;
  font-weight: 600;
  font-size: 0.9rem;
  backdrop-filter: blur(10px);
  letter-spacing: 0.5px;
}

/* Sidebar Overlay */
.sidebar-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(2px);
  z-index: 99;
  opacity: 0;
  animation: fadeIn 0.3s ease-out forwards;
}

@keyframes fadeIn {
  to { opacity: 1; }
}

/* Left Sidebar */
.left-sidebar {
  width: 250px; /* Narrower than before (was 300px) */
  background: rgba(15, 23, 42, 0.95);
  backdrop-filter: blur(20px);
  border-right: 2px solid rgba(251, 191, 36, 0.3);
  padding: 1.5rem 1rem; /* Reduced padding for narrower width */
  overflow-y: auto;
  position: fixed;
  height: calc(100vh - 140px);
  left: 0;
  top: 140px;
  z-index: 100;
  transform: translateX(-100%);
  transition: transform 0.3s ease-out;
  box-shadow: 4px 0 20px rgba(0, 0, 0, 0.3);
}

.left-sidebar.sidebar-open {
  transform: translateX(0);
}

.left-sidebar.sidebar-closed {
  transform: translateX(-100%);
}

.sidebar-section {
  margin-bottom: 2rem;
}

.sidebar-title {
  font-size: 1rem; /* Slightly smaller for narrower sidebar */
  font-weight: 700;
  color: #fbbf24;
  margin-bottom: 1rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.sidebar-menu {
  list-style: none;
  padding: 0;
  margin: 0;
}

.sidebar-menu-item {
  margin-bottom: 0.5rem;
}

.sidebar-menu-link {
  display: flex;
  align-items: center;
  gap: 0.75rem; /* Reduced gap for narrower sidebar */
  padding: 0.75rem; /* Reduced padding */
  border-radius: 8px; /* Slightly smaller border radius */
  cursor: pointer;
  transition: all 0.3s ease;
  color: rgba(255, 255, 255, 0.8);
  text-decoration: none;
  font-weight: 500;
  font-size: 0.9rem; /* Slightly smaller text */
  border: 1px solid transparent;
}

.sidebar-menu-link:hover {
  background: rgba(251, 191, 36, 0.1);
  border-color: rgba(251, 191, 36, 0.3);
  color: #fbbf24;
  transform: translateX(6px); /* Reduced transform distance */
}

.sidebar-menu-link.active {
  background: rgba(251, 191, 36, 0.2);
  border-color: rgba(251, 191, 36, 0.5);
  color: #fbbf24;
  font-weight: 700;
}

/* Content Area with Dynamic Margins */
.content-area {
  flex: 1;
  margin-left: 0; /* No margin by default since sidebar is hidden */
  padding: 2rem;
  min-height: calc(100vh - 140px);
  transition: margin-left 0.3s ease-out;
}

.content-with-sidebar {
  margin-left: 0; /* Keep at 0 since sidebar is overlay on mobile */
}

.content-no-sidebar {
  margin-left: 0;
}

.header-left {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.header-title {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.header-icon {
  font-size: 3rem;
  background: linear-gradient(135deg, #fbbf24, #f59e0b);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  filter: drop-shadow(2px 2px 4px rgba(0, 0, 0, 0.3));
}

.header-title h1 {
  margin: 0;
  font-size: 2.5rem;
  font-weight: 800;
  background: linear-gradient(135deg, #ffffff, #fbbf24);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
  letter-spacing: -0.5px;
}

.header-subtitle {
  font-size: 1rem;
  font-weight: 500;
  color: rgba(255, 255, 255, 0.9);
  margin-top: 0.25rem;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
}

/* Responsive Design for New Layout */
@media (max-width: 768px) {
  .header-content {
    padding: 1rem;
    flex-direction: column;
    gap: 1rem;
  }
  
  .header-title h1 {
    font-size: 1.5rem;
  }
  
  .header-subtitle {
    font-size: 0.875rem;
  }
  
  .left-sidebar {
    width: 100%;
    position: static;
    height: auto;
  }
  
  .content-area {
    margin-left: 0;
  }
  
  .main-content {
    padding: 1rem;
  }
  
  .header-right {
    flex-direction: column;
    gap: 1rem;
  }
  
  .live-datetime {
    align-items: center;
  }
}

/* Main Content within Content Area */
.main-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0;
  position: relative;
  z-index: 1;
}

/* Stats Grid */
.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
}

.stat-card {
  background: rgba(30, 41, 59, 0.8);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 16px;
  padding: 1.5rem;
  backdrop-filter: blur(20px);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.stat-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, #fbbf24, #f59e0b);
}

.stat-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.3);
  border-color: rgba(251, 191, 36, 0.3);
}

.stat-card.info::before {
  background: linear-gradient(90deg, #3b82f6, #2563eb);
}

.stat-card.warning::before {
  background: linear-gradient(90deg, #f59e0b, #d97706);
}

.stat-card.danger::before {
  background: linear-gradient(90deg, #ef4444, #dc2626);
}

.stat-card.success::before {
  background: linear-gradient(90deg, #10b981, #059669);
}

.stat-icon {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  opacity: 0.8;
}

.stat-number {
  font-size: 2.5rem;
  font-weight: 800;
  color: #ffffff;
  margin-bottom: 0.5rem;
  line-height: 1;
}

.stat-label {
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.7);
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.stat-change {
  font-size: 0.875rem;
  font-weight: 600;
  margin-top: 0.5rem;
}

.stat-change.positive {
  color: #10b981;
}

.stat-change.negative {
  color: #ef4444;
}

/* Cards */
.card {
  background: rgba(30, 41, 59, 0.9);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 16px;
  padding: 2rem;
  margin-bottom: 2rem;
  backdrop-filter: blur(20px);
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.2);
}

.card-header {
  margin-bottom: 1.5rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.card-title {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  font-size: 1.5rem;
  font-weight: 700;
  color: #ffffff;
  margin: 0;
}

/* Buttons */
.btn {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 12px;
  font-weight: 600;
  font-size: 0.875rem;
  cursor: pointer;
  transition: all 0.3s ease;
  text-decoration: none;
  position: relative;
  overflow: hidden;
  letter-spacing: 0.5px;
}

.btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.5s;
}

.btn:hover::before {
  left: 100%;
}

.btn-primary {
  background: linear-gradient(135deg, #f59e0b, #d97706);
  color: white;
  border: 2px solid transparent;
}

.btn-primary:hover {
  background: linear-gradient(135deg, #d97706, #b45309);
  transform: translateY(-2px);
  box-shadow: 0 10px 15px -3px rgba(217, 119, 6, 0.3);
}

.btn-secondary {
  background: rgba(71, 85, 105, 0.8);
  color: white;
  border: 2px solid rgba(255, 255, 255, 0.2);
}

.btn-secondary:hover {
  background: rgba(71, 85, 105, 1);
  border-color: rgba(255, 255, 255, 0.3);
  transform: translateY(-2px);
}

.btn-success {
  background: linear-gradient(135deg, #10b981, #059669);
  color: white;
  border: 2px solid transparent;
}

.btn-success:hover {
  background: linear-gradient(135deg, #059669, #047857);
  transform: translateY(-2px);
  box-shadow: 0 10px 15px -3px rgba(16, 185, 129, 0.3);
}

.btn-danger {
  background: linear-gradient(135deg, #ef4444, #dc2626);
  color: white;
  border: 2px solid transparent;
}

.btn-danger:hover {
  background: linear-gradient(135deg, #dc2626, #b91c1c);
  transform: translateY(-2px);
  box-shadow: 0 10px 15px -3px rgba(239, 68, 68, 0.3);
}

.btn-sm {
  padding: 0.5rem 1rem;
  font-size: 0.75rem;
}

.btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none !important;
}

/* Notification Bell */
.notification-bell {
  background: rgba(255, 255, 255, 0.2);
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  backdrop-filter: blur(10px);
}

.notification-bell:hover {
  background: rgba(255, 255, 255, 0.3);
  border-color: rgba(255, 255, 255, 0.5);
  transform: scale(1.1);
}

/* Badges */
.badge {
  display: inline-flex;
  align-items: center;
  padding: 0.25rem 0.75rem;
  border-radius: 12px;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.badge-priority-high {
  background: linear-gradient(135deg, #ef4444, #dc2626);
  color: white;
}

.badge-priority-medium {
  background: linear-gradient(135deg, #f59e0b, #d97706);
  color: white;
}

.badge-priority-low {
  background: linear-gradient(135deg, #10b981, #059669);
  color: white;
}

.badge-status-completed {
  background: linear-gradient(135deg, #10b981, #059669);
  color: white;
}

.badge-status-pending {
  background: linear-gradient(135deg, #f59e0b, #d97706);
  color: white;
}

.badge-status-overdue {
  background: linear-gradient(135deg, #ef4444, #dc2626);
  color: white;
  animation: pulse 2s infinite;
}

/* Forms */
.form-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.5rem;
  margin-bottom: 1.5rem;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.form-label {
  font-weight: 600;
  color: #ffffff;
  font-size: 0.875rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.form-input,
.form-select {
  padding: 0.75rem 1rem;
  border: 2px solid rgba(255, 255, 255, 0.2);
  border-radius: 12px;
  background: rgba(30, 41, 59, 0.8);
  color: #ffffff;
  font-size: 1rem;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
}

.form-input:focus,
.form-select:focus {
  outline: none;
  border-color: #fbbf24;
  box-shadow: 0 0 0 3px rgba(251, 191, 36, 0.2);
  background: rgba(30, 41, 59, 0.9);
}

.form-input::placeholder {
  color: rgba(255, 255, 255, 0.5);
}

/* Task Components */
.task-list {
  background: rgba(30, 41, 59, 0.8);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 16px;
  overflow: hidden;
  backdrop-filter: blur(20px);
}

.task-header {
  background: linear-gradient(135deg, #475569, #334155);
  padding: 1.5rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.task-filters {
  display: flex;
  gap: 1rem;
  margin-top: 1rem;
  flex-wrap: wrap;
}

.task-filters .form-group {
  margin-bottom: 0;
  min-width: 150px;
}

.task-item {
  padding: 1.5rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  transition: all 0.3s ease;
}

.task-item:last-child {
  border-bottom: none;
}

.task-item:hover {
  background: rgba(51, 65, 85, 0.5);
  transform: translateX(8px);
}

.task-item.task-overdue {
  border-left: 4px solid #ef4444;
  background: rgba(239, 68, 68, 0.1);
}

.task-meta {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 1rem;
  flex-wrap: wrap;
  gap: 1rem;
}

.task-title {
  font-size: 1.25rem;
  font-weight: 700;
  color: #ffffff;
  margin-bottom: 0.25rem;
}

.task-property {
  font-size: 0.875rem;
  color: rgba(255, 255, 255, 0.7);
  font-weight: 500;
}

.task-badges {
  display: flex;
  gap: 0.5rem;
  flex-wrap: wrap;
}

.task-description {
  color: rgba(255, 255, 255, 0.8);
  margin-bottom: 1rem;
  font-style: italic;
}

.task-details {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1rem;
  margin-bottom: 1.5rem;
}

.task-detail {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.task-detail-label {
  font-size: 0.75rem;
  color: rgba(255, 255, 255, 0.6);
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.task-detail-value {
  font-size: 0.875rem;
  color: #ffffff;
  font-weight: 500;
}

.task-actions {
  display: flex;
  gap: 0.75rem;
  flex-wrap: wrap;
}

/* Modals */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  backdrop-filter: blur(5px);
  animation: fadeIn 0.3s ease-out;
}

.modal-content {
  background: rgba(15, 23, 42, 0.95);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 16px;
  max-width: 500px;
  width: 90vw;
  max-height: 90vh;
  overflow: hidden;
  backdrop-filter: blur(20px);
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.5);
  animation: slideIn 0.3s ease-out;
}

.modal-content.large-modal {
  max-width: 800px;
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  background: linear-gradient(135deg, #475569, #334155);
}

.modal-header h2 {
  margin: 0;
  color: white;
  display: flex;
  align-items: center;
  gap: 0.75rem;
  font-size: 1.25rem;
  font-weight: 700;
}

.modal-close {
  background: none;
  border: none;
  color: white;
  cursor: pointer;
  padding: 0.5rem;
  border-radius: 8px;
  transition: all 0.2s ease;
}

.modal-close:hover {
  background: rgba(255, 255, 255, 0.1);
  transform: scale(1.1);
}

.modal-body {
